.scoreboard-container {
  background: var(--color-bg-table);
  max-width: 1100px;
  box-shadow: 0 8px 32px var(--color-shadow);
  border-radius: 24px;
  margin: 24px auto 0;
  padding: 32px 32px 40px;
  overflow-x: auto;
}

.scoreboard-table {
  border-collapse: separate;
  border-spacing: 0 8px;
  color: #fff;
  table-layout: fixed;
  background: none;
  width: 100%;
  min-width: 900px;
  max-width: 100%;
}

.scoreboard-table th, .scoreboard-header-row {
  font-size: var(--font-size-large);
  padding: var(--padding-table-cell);
  background: var(--color-header-row);
  color: var(--color-header-text);
  text-align: left;
  letter-spacing: .03em;
  cursor: pointer;
  white-space: nowrap;
  font-weight: 600;
  transition: background .15s;
  box-shadow: 0 2px 8px #0002;
}

.scoreboard-table th:hover {
  background: #78b4ff21;
}

.scoreboard-table td {
  font-size: var(--font-size-table);
  padding: var(--padding-table-cell);
}

.scoreboard-row {
  background: none;
  border-bottom: none;
  height: 54px;
  transition: background .18s;
}

.scoreboard-row:nth-child(2n) {
  background: var(--color-row-alt);
}

.scoreboard-row:hover {
  background: var(--color-row-hover);
}

.scoreboard-rank-header, .scoreboard-table th.scoreboard-rank-header, .scoreboard-table td.scoreboard-rank {
  width: 48px;
  min-width: 48px;
  max-width: 48px;
}

.scoreboard-player-header, .scoreboard-table th.scoreboard-player-header, .scoreboard-table td.scoreboard-player-cell {
  text-overflow: ellipsis;
  width: 220px;
  min-width: 160px;
  max-width: 240px;
  overflow: hidden;
}

.scoreboard-table th.scoreboard-placement-header-score, .scoreboard-table td.scoreboard-player-score, .scoreboard-table th.scoreboard-placement-header-games, .scoreboard-table td.scoreboard-player-games, .scoreboard-table th.scoreboard-placement-header-avgscore, .scoreboard-table td.scoreboard-player-avgscore {
  width: 110px;
  min-width: 90px;
  max-width: 120px;
}

@media (width <= 700px) {
  .scoreboard-container {
    padding: 8px 2vw 16px;
  }

  .scoreboard-header-row, .scoreboard-table th {
    padding: 6px 0;
    font-size: 1rem;
    font-weight: 600;
  }

  .scoreboard-player-score, .scoreboard-player-avgscore, .scoreboard-player-games, .scoreboard-player-name {
    text-align: center;
    padding: 4px 0;
    font-size: 1.1rem;
    font-weight: 600;
  }

  .scoreboard-player-cell {
    padding: 0 4px;
  }
}

@media (width <= 480px) {
  .scoreboard-table th, .scoreboard-table td {
    padding: 8px 4px;
    font-size: .95rem;
    font-weight: 600;
  }
}
